/* src/app/components/party/party.component.scss */
.party-nav {
  display: flex;
  gap: 1rem;
  padding: 1.5rem;
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
  flex-wrap: wrap;
  margin: 0 auto;
  width: fit-content;
  justify-content: center;
}
.party-btn {
  position: relative;
  padding: 0.875rem 1.75rem;
  background: #08CC0A;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
  box-shadow: 0 2px 4px rgba(8, 204, 10, 0.2);
}
.party-btn:hover {
  background: #07b309;
  transform: translateY(-2px);
  box-shadow: 0 4px 8px rgba(8, 204, 10, 0.3);
}
.party-btn:active {
  transform: translateY(0);
  box-shadow: 0 1px 2px rgba(8, 204, 10, 0.2);
}
.party-btn.active {
  background: #06a008;
  box-shadow: inset 0 2px 4px rgba(0, 0, 0, 0.2);
}
.party-btn .counter {
  position: absolute;
  top: -8px;
  right: -8px;
  background: #ff4444;
  color: white;
  font-size: 0.75rem;
  font-weight: 700;
  padding: 0.25rem 0.5rem;
  border-radius: 12px;
  min-width: 24px;
  text-align: center;
}
a {
  text-decoration: none;
}
